html, body {
  font-family: 'Playfair Display', serif;
  font-weight: 400;
}

section {
  padding: 1.5rem;
}

.hr-color {
  border: 0.1rem solid;
  width: 10%;
  -webkit-animation: rotateBorderColor 8s ease infinite alternate-reverse;
          animation: rotateBorderColor 8s ease infinite alternate-reverse; 
}

.border-color {
  border: 0.2rem solid;
  -webkit-animation: rotateBorderColor 8s ease infinite alternate-reverse;
          animation: rotateBorderColor 8s ease infinite alternate-reverse; 
}

.text-rot-color {
  -webkit-animation: rotateColor 8s ease infinite alternate-reverse;
          animation: rotateColor 8s ease infinite alternate-reverse;
}

.main-app{
  -webkit-animation: fadeIn 2s ease;
          animation: fadeIn 2s ease;
}

.color-button:hover {
  -webkit-animation: rotateBorderColor 8s ease infinite alternate-reverse;
          animation: rotateBorderColor 8s ease infinite alternate-reverse;
}

@-webkit-keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@-webkit-keyframes rotateBorderColor {
  0% { border-color: #dd2b2b }
  50% { border-color: #589e21 }
  100% { border-color: #a6dcee }
}

@keyframes rotateBorderColor {
  0% { border-color: #dd2b2b }
  50% { border-color: #589e21 }
  100% { border-color: #a6dcee }
}

@-webkit-keyframes rotateColor {
  0% { color: #dd2b2b }
  50% { color: #589e21 }
  100% { color: #a6dcee }
}

@keyframes rotateColor {
  0% { color: #dd2b2b }
  50% { color: #589e21 }
  100% { color: #a6dcee }
}

.margin-0{
  margin: 0;
}

.pad-0 {
  padding: 0;
}

.pad-0-5 {
  padding: 0.5rem;
}

.pad-1 {
  padding: 1rem;
}

.pad-2 {
  padding: 2rem;
}


